.ReasonItem {
  width: 300px;
  padding: 5px 0 35px 0;
  display: grid;
  justify-items: center; }
  .ReasonItem__Image {
    width: 250px;
    height: 250px;
    background-size: cover;
    border-radius: 50%;
    border: 5px solid white;
    box-shadow: 1px 1px 10px lightgray;
    cursor: pointer;
    transition: transform 0.1s ease-in-out; }
    .ReasonItem__Image:hover {
      transform: scale(1.02);
      box-shadow: 1px 1px 20px lightgray; }
  .ReasonItem__Title {
    margin-top: 15px;
    font-size: 25px;
    color: #d3531a;
    font-weight: 700;
    text-align: center; }
  .ReasonItem__Content {
    text-align: center;
    padding: 20px 0 20px 0; }
