.parallax {
  height: 150px;
  position: relative;
  overflow: hidden; }
  .parallax img {
    width: 100%;
    height: auto;
    position: absolute; }
  .parallax h2 {
    font-size: 25px;
    position: absolute;
    top: 0;
    left: 100px;
    text-shadow: 2px 8px 6px rgba(0, 0, 0, 0.2), 0px -5px 35px rgba(255, 255, 255, 0.3); }

@media (min-width: 576px) {
  .parallax {
    height: 200px; }
    .parallax img {
      width: 100%; }
    .parallax h2 {
      font-size: 25px; } }

@media (min-width: 768px) {
  .parallax {
    height: 200px; }
    .parallax h2 {
      font-size: 25px; } }

@media (min-width: 992px) {
  .parallax {
    height: 350px; }
    .parallax h2 {
      font-size: 25px; } }

@media (min-width: 1200px) {
  .parallax {
    height: 500px; }
    .parallax h2 {
      left: 300px;
      font-size: 75px; } }
