.Title {
  position: relative;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
  padding: 10px 0 15px 0; }
  .Title__Text {
    font-weight: 700;
    display: inline-block;
    background: white;
    padding: 0 15px; }
  .Title__Line {
    z-index: -1;
    width: 100%;
    position: absolute;
    top: 23px;
    right: 0;
    left: 0;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 1px solid #d3531a;
    display: inline-block; }

@media (min-width: 576px) {
  .Title__Line {
    width: 420px; } }

@media (min-width: 768px) {
  .Title {
    font-size: 25px; }
    .Title__Line {
      width: 620px;
      top: 28px; } }

@media (min-width: 992px) {
  .Title {
    font-size: 30px; }
    .Title__Line {
      width: 720px;
      top: 33px; } }
