.SideNav {
  height: 100%;
  max-height: 100%;
  overflow: auto;
  width: 280px;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  background-color: #f1f1f1;
  transform: translateX(-280px);
  opacity: 1;
  padding-top: 0;
  transition: all 0.5s cubic-bezier(0.7, 0.01, 0.25, 0.8); }
  .SideNav__Menu__Header {
    font-family: "lobster";
    color: #d3531a;
    font-size: 33px;
    padding: 10px 0 5px 20px; }
  .SideNav__Link {
    padding: 10px 10px 10px 20px;
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;
    display: block;
    color: #686f7a;
    transition: 0.05s; }
    .SideNav__Link:hover {
      text-decoration: none;
      background: #e2e3e4;
      color: #d3531a; }

#sidebar_overlay {
  z-index: 2;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none; }
