.NewsItem__Header {
  cursor: pointer; }

.NewsItem__Image {
  width: 100%;
  transition: transform 0.2s ease-in-out;
  margin-bottom: 7px;
  border-radius: 7px; }
  .NewsItem__Image:hover {
    transform: scale(1.02); }

.NewsItem__Tag {
  font-size: 10px;
  padding: 3px 20px;
  border: 1px solid lightgray;
  display: inline-block;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.1s ease-in-out; }
  .NewsItem__Tag:hover {
    background: #d3531a;
    color: white;
    border: 1px solid #d3531a; }

.NewsItem__Title {
  font-weight: 700;
  padding: 20px 0;
  font-size: 22px;
  cursor: pointer; }

.NewsItem__Footer__Datetime {
  font-size: 15px; }

.NewsItem__Footer__Like {
  font-size: 11px;
  cursor: pointer;
  background: #279ed4;
  color: white;
  border-radius: 5px;
  padding: 3px 6px;
  margin-left: 10px;
  font-weight: 700; }
  .NewsItem__Footer__Like:hover {
    background: #154183; }
