.swiper-container {
  width: 100%;
  height: 500px; }

.swiper-slide {
  background-position: center;
  background-size: cover;
  position: relative; }

.SwiperSlide__Content {
  background: rgba(255, 255, 255, 0.8);
  position: absolute;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  padding: 5px 15px 15px 15px;
  bottom: 40px; }
  .SwiperSlide__Content .title {
    text-align: center;
    font-family: 'lobster';
    margin-bottom: 7px;
    text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.7);
    font-size: 39px;
    font-weight: 700; }
  .SwiperSlide__Content .subtitle {
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    font-size: 25px; }
  .SwiperSlide__Content .text {
    font-size: 17px;
    text-align: justify;
    line-height: 1.3; }

.chucTetContent {
  background: rgba(255, 255, 255, 0.8);
  position: absolute;
  width: 340px;
  left: 70px;
  padding: 10px;
  bottom: 160px;
  font-family: 'Pacifico', cursive;
  font-size: 20px; }
  .chucTetContent div {
    margin-bottom: 7px; }

@media (min-width: 1200px) {
  .swiper-container {
    width: 100%;
    height: 700px; } }
