.Header {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  z-index: 100; }
  .Header__Contact {
    padding: 10px 15px;
    text-align: right;
    font-size: 16px !important;
    background-color: #b59153;
    color: white; }
    .Header__Contact__Link {
      color: white; }
      .Header__Contact__Link:hover {
        color: #f1f1f1;
        text-decoration: none; }
    .Header__Contact i {
      cursor: pointer; }
    .Header__Contact i, .Header__Contact span {
      padding: 0 5px; }
    .Header__Contact span {
      font-weight: 100; }
    .Header__Contact__Content {
      display: none; }
  .Header__Navigation {
    position: relative;
    padding: 6px 13px;
    background-color: rgba(250, 244, 193, 0.85); }
  .Header__Logo {
    width: 65px; }
  .Header__Flag {
    position: absolute;
    top: 17px;
    right: 68px; }
    .Header__Flag__Item {
      cursor: pointer;
      width: 25px;
      margin-left: 7px; }
    .Header__Flag__Choosed {
      filter: brightness(50%); }

.Header__Nav {
  position: absolute;
  display: none;
  top: 10px;
  left: 100px; }
  .Header__Nav__Item {
    display: inline-block;
    padding: 10px 10px 11px 15px;
    font-size: 17px;
    font-weight: 700;
    color: #333;
    cursor: pointer; }
    .Header__Nav__Item:hover {
      color: #d3531a;
      border-bottom: 5px solid #d3531a;
      text-decoration: none; }

.Active {
  color: #d3531a;
  border-bottom: 5px solid #d3531a; }

@media (min-width: 768px) {
  .Header__Contact__Content {
    display: inline-block; } }

@media (min-width: 992px) {
  .Header__Nav {
    display: block; }
  .Header__Flag {
    top: 23px;
    right: 20px; } }
