.ItemServices {
  padding: 20px 15px;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  gap: 15px; }

@media (min-width: 576px) {
  .ItemServices {
    grid-template-columns: 1fr 1fr; } }

@media (min-width: 992px) {
  .ItemServices {
    grid-template-columns: 1fr 1fr 1fr 1fr; } }

@media (min-width: 1200px) {
  .ItemServices {
    width: 1200px;
    margin: auto;
    gap: 30px; } }
