.Reason__Content {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center; }

@media (min-width: 576px) {
  .Reason__Content {
    grid-template-columns: 1fr 1fr; } }

@media (min-width: 768px) {
  .Reason__Content {
    grid-template-columns: 1fr 1fr 1fr; } }

@media (min-width: 1200px) {
  .Reason {
    width: 1200px;
    margin: auto; } }
