.Footer {
  height: 100%;
  background: #f1f1f1;
  padding: 15px; }
  .Footer__Content {
    height: 100%;
    display: grid;
    gap: 10px;
    grid-template-columns: 1fr; }
    .Footer__Content__Left {
      display: grid;
      gap: 10px;
      grid-template-columns: 1fr 1fr; }
    .Footer__Content__Right {
      height: 100%; }
  .Footer__Website {
    font-family: 'Lobster', cursive; }
    .Footer__Website__Company {
      font-size: 45px;
      margin-bottom: 0; }
    .Footer__Website__Address {
      margin-top: 0; }
  .Footer__Address {
    display: grid;
    grid-template-columns: 1fr; }
    .Footer__Address__Title {
      font-weight: 700; }
  .Footer__Menu__Link {
    color: #333; }
  .Footer__Title {
    font-weight: 700;
    font-size: 17px; }
  .Footer__Column {
    margin-top: 20px; }
  .Footer__Info {
    margin-top: 5px;
    display: grid;
    color: #333;
    grid-template-columns: 22px 1fr; }
    .Footer__Info__Icon {
      width: 20px; }
    .Footer__Info__Text {
      font-size: 13px;
      line-height: 25px; }

.map__title {
  display: flex; }
  .map__title__item {
    font-weight: bold;
    cursor: pointer;
    padding: 15px; }
    .map__title__item:hover {
      color: #d3531a; }

.map--active {
  color: #d3531a; }

@media (min-width: 576px) {
  .Footer__Content__Left {
    grid-template-columns: 1fr 1fr 1fr; } }

@media (min-width: 768px) {
  .Footer__Content {
    grid-template-columns: 1fr 1fr; }
    .Footer__Content__Left {
      grid-template-columns: 1fr 1fr; } }

@media (min-width: 992px) {
  .Footer__Content__Left {
    grid-template-columns: 1fr 1fr 1fr; } }
