.ItemService {
  position: relative;
  width: 300px;
  height: 400px;
  border-radius: 7px;
  overflow-x: hidden; }
  .ItemService__Img {
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    cursor: pointer;
    transition: filter 0.15s ease-in-out; }
    .ItemService__Img:hover {
      filter: brightness(50%); }
  .ItemService__Title {
    position: absolute;
    bottom: 30px;
    font-size: 23px;
    text-align: center;
    letter-spacing: -2px;
    text-transform: uppercase;
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    padding: 30px 0;
    color: #ffd900; }

@media (min-width: 992px) {
  .ItemService {
    width: 100%; } }

@media (min-width: 1200px) {
  .ItemService {
    width: 100%; } }
