.MenuIcon {
  width: 40px;
  height: 40px;
  top: 16px;
  right: 13px;
  position: absolute;
  transform: rotate(0deg);
  transition: 0.3s ease-in-out;
  z-index: 3;
  cursor: pointer; }
  .MenuIcon__Span {
    display: block;
    position: absolute;
    height: 5px;
    width: 100%;
    background: #d3531a;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: 0.15s ease-in-out; }
    .MenuIcon__Span:nth-child(1) {
      top: 0px;
      transform-origin: left center; }
    .MenuIcon__Span:nth-child(2) {
      top: 12px;
      transform-origin: left center; }
    .MenuIcon__Span:nth-child(3) {
      top: 24px;
      transform-origin: left center; }

.MenuIcon.Open .MenuIcon__Span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  top: 0px;
  left: 0px; }

.MenuIcon.Open .MenuIcon__Span:nth-child(2) {
  width: 0%;
  opacity: 0; }

.MenuIcon.Open .MenuIcon__Span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 28px;
  left: 0px; }

@media (min-width: 992px) {
  .MenuIcon {
    display: none; } }
